import { useLocation, useSearchParams } from "react-router-dom";
import UserMemberMainWrapper from "../../Layout/UserMemberTab/UserMemberMainWrapper";
import QvipleLoader from "../../Loader/QvipleLoader";
import { Suspense, lazy } from "react";
import UserMemberTabsWrapper from "../../Ui/UserMember/UserMemberTabsWrapper";
import CommitteeRndManagerProfile from "./CommitteeRndManagerProfile";
import RndMembers from "./RndMembers/RndMembers";
import RndMou from "./RndMou/RndMou";
import RndProject from "./RndProject/RndProject";
import RndActivities from "./RndActivities/RndActivities";
import RndResearchPaper from "./RndResearchPaper/RndResearchPaper";
const CommitteeRndManager = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();

  return (
    <>
      <UserMemberMainWrapper>
        <Suspense fallback={<QvipleLoader />}>
          {!searchParams.get("type") ||
          ["meeting", "about"]?.includes(searchParams.get("type")) ? (
            <UserMemberTabsWrapper>
              <CommitteeRndManagerProfile qcid={getQuery.state?.qcid} />
            </UserMemberTabsWrapper>
          ) : searchParams.get("type") === "mou" ? (
            <RndMou qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "project" ? (
            <RndProject qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "activities" ? (
            <RndActivities qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "research-paper" ? (
            <RndResearchPaper qcid={getQuery.state?.qcid} />
          ) : searchParams.get("type") === "member" ? (
            <RndMembers qcid={getQuery.state?.qcid} />
          ) : null}
        </Suspense>
      </UserMemberMainWrapper>
    </>
  );
};

export default CommitteeRndManager;
